body{
  margin: 0;
  padding: 0;
}
h6::before{
  content: "";
  position: absolute;
  background: url('https://png.pngtree.com/back_origin_pic/04/59/72/04cb82301085d2fab8fb8aecf121702e.jpg')no-repeat center center/cover;
  width: 100%;
  height: 100%;
  filter: blur(8px);
}
.box{
  position: absolute;
    top: 28%;
    left: 34%;
    width: 35%;
    display: flex;
    border-radius: 20px;
    padding: 36px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(121 121 121 / 30%);
    color: white;
}
@media (max-width:500px){
 h6::before{
  width: 112%;
  height: 113%;
 }
 .box{
  left: 11%;
    width: 90%;
 }
 .range1{
  width: 100% !important;
 }
 input[type=text]{
  width: 230px !important;
 }
 .range2{
  width: 90% !important;
 }
}
@media (max-width:452px){
 h6::before{
  width: 112%;
  height: 113%;
 }
 .box{
  left: 11%;
    width: 90%;
 }
 .range1{
  width: 100% !important;
 }
 input[type=text]{
  width: 200px !important;
 }
 .range2{
  width: 100% !important;
 }
}